import Link from 'next/link'
import {styled} from 'styled-components'
import React from 'react'


const Button = styled.button`
  color: palevioletred;
  font-size: 1em;
  margin: 1em;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 3px;
`;
const Index = () => {
  
    return(
    <div>
        <StyledLink href="/people">
     <Button>  List People Page</Button>
    </StyledLink>
      <p>Hello Famoco</p>
    </div>
  )
}
  
  export default Index


